.sidebar {
    height: 100vh;
    width: 250px;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #2c3e50;
    padding-top: 20px;
  }
  
  .sidebar h3 {
    color: #ecf0f1;
    text-align: center;
  }
  
  .sidebar ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar ul li {
    padding: 10px 20px;
  }
  
  .sidebar ul li a {
    color: #ecf0f1;
    text-decoration: none;
    font-size: 18px;
  }
  
  .sidebar ul li a:hover {
    background-color: #34495e;
    color: #fff;
    padding-left: 10px;
    transition: 0.3s;
  }